@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
:root {
    --text: #eeeeee;
    --background: #222831;
    --background2: #303844;
    --primary: #52C1FF;
    --nav-height: 70px;
}

*, body {
    margin: 0;
    box-sizing: border-box;
    color: #eeeeee;
    color: var(--text);
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
}

.App {
    background: #222831;
    background: var(--background);
}

.workspace {
    width: 80%;
    margin: auto;
    z-index: 2;
}

button {
    font-size: 1.5rem;
    font-weight: 500;
    padding: 0.5rem 2rem;
    border: 2px solid #52C1FF;
    border: 2px solid var(--primary);
    box-sizing: border-box;
    border-radius: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    cursor: pointer;
    background: #222831;
    background: var(--background);
}

button>img {
    width: 1.15rem;
    height: 1.15rem;
    object-fit: contain;
}

button[type="primary"] {
    border: none;
    background: #52C1FF;
    background: var(--primary);
}

input {
    background: #222831;
    background: var(--background);
    border: 2px solid #52C1FF;
    border: 2px solid var(--primary);
    border-radius: 0.75rem;
    padding: 1rem 1.5rem;
    color: #eeeeee;
    color: var(--text);
    box-sizing: border-box;
    flex: 1 1;
  }
  
  input:focus-visible {
    outline: 2px solid #52C1FF;
    outline: 2px solid var(--primary);
  }
  
  ::-webkit-input-placeholder {
    font-weight: 700;
  }
  
  ::placeholder {
    font-weight: 700;
  }



@media only screen and (min-width: 1201px) and (max-width: 1400px) {
    .workspace {
      width: 85%;
    }
}
  
@media only screen and (max-width: 1200px) {
.workspace {
    width: 90%;
}
}
.navbar {
    width: 100%;
    height: var(--nav-height);
    background: transparent;
    z-index: 20;
    position: fixed;
    top: 0;
}

.navbar_main {
    height: 100%;
    display: flex;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
}

.navbar_main_bg {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.go_back_icon {
    width: 30px;
    height: 30px;
    display: flex;
    cursor: pointer;
}

.go_back_icon img {
    margin: auto;
    width: 70%;
    height: 70%;
    object-fit: contain;
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg);
}
.scanner_section {
    margin: 0px !important;
}

.viewport {
    height: 100vh;
}

.viewport video, .viewport canvas {
    height: 100%;
    width: 100%;
    object-fit: cover;
    position: absolute;
}

.barcode_scanner {
    position: relative;
}

.barcode_overlay_section_bg {
    position: absolute;
    z-index: 10;
    width: 100%;
    bottom: 0;
    padding: 1rem 0px;
    background: var(--background);
}

.barcode_overlay_section {
    display: flex;
    align-items: center;
    grid-gap: min(20px, 2vw);
    gap: min(20px, 2vw);
}

.barcode_overlay_section>input {
    height: 50px;
    min-width: 100px;
}

.barcode_overlay_section>button {
    padding: 0px;
    width: 50px;
    height: 50px;
    border-radius: 0.75rem;
}

.loading_bg {
    width: 100vw;
    height: 100vh;
    position: absolute;
    background: radial-gradient(transparent, black);
    z-index: 11;
}

.loading_bg>img {
    width: 50px;
    height: 50px;
    position: absolute;
    z-index: 11;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.stores_list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    min-height: calc(100vh - var(--nav-height));
    grid-gap: 1.25rem;
    gap: 1.25rem;
    padding-top: 20px;
    padding-bottom: 20px;
}


.top_location_section {
    display: flex;
    width: 100%;
    height: 50px;
    align-items: center;
    justify-content: space-between;
    background: var(--background2);
    grid-gap: min(1.25rem, 2vw);
    gap: min(1.25rem, 2vw);
    padding-left: 1rem;
    padding-right: 1rem;
    border-radius: 0.75rem;
}

.location_icon {
    width: 24px;
    height: 24px;
}
.location_icon img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.location_text {
    flex: 1 1;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-weight: 500;
    opacity: 0.7;
}

.location_edit_icon {
    width: 24px;
    height: 24px;
    display: flex;
}
.location_edit_icon img {
    margin: auto;
}

.stores_list_title {
    font-size: 1.5rem;
    font-weight: bold;
}

.product_cards_container {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    width: 100%;
}

.product_card {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 1rem;
    background: var(--background2);
    border-radius: 0.75rem;
    grid-gap: 1rem;
    gap: 1rem;
}

.product_card_image {
    width: 30%;
    aspect-ratio: 1 / 1;
    border-radius: 0.375rem;
    display: flex;
    overflow: hidden;
}
.product_card_image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.product_card_text_container {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    grid-gap: min(1rem, 2vw);
    gap: min(1rem, 2vw);
}

.product_card_title {
    font-weight: bold;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
}

.product_card_subtitle {
    font-size: 0.875rem;
    opacity: 0.6;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
}

.product_card_rating {
    width: 100%;
    display: flex;
    height: 24px;
    max-width: 140px;
}
.product_card_rating img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.home {
    max-width: 500px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.home_logo, .home_image {
    width: 90%;
}

.home_logo svg, .home_image svg {
    width: 100%;
    height: auto;
}

.home_text {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.home_title{
    font-size: 1.75rem;
    font-weight: 500;
}

.home_subtitle{
    font-size: 1rem;
}

.split {
  height: 100%;
  width: 50%;
  position: fixed;
  z-index: 1;
  top: 0;
  overflow-x: hidden;
  padding-top: 20px;
}

.left {
  left: 0;
  background-color: #222831;
}

.right {
  right: 0;
  background-color: #0e1525;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
}

.centered img {
  width: 150px;
}

.content {
  top: 10%;
  left: 35%;
  position: absolute;
}
.circle {
  stroke-dasharray: 1194;
  stroke-dashoffset: 1194;
  -webkit-animation: dc 1s ease-in-out;
          animation: dc 1s ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.tick {
  stroke-dasharray: 350;
  stroke-dashoffset: 350;
  -webkit-animation: dt 0.8s ease-out;
          animation: dt 0.8s ease-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 0.95s;
          animation-delay: 0.95s;
}
@-webkit-keyframes dc {
  from {
    stroke-dashoffset: 1194;
  }
  to {
    stroke-dashoffset: 2388;
  }
}
@keyframes dc {
  from {
    stroke-dashoffset: 1194;
  }
  to {
    stroke-dashoffset: 2388;
  }
}
@-webkit-keyframes dt {
  from {
    stroke-dashoffset: 350;
  }
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes dt {
  from {
    stroke-dashoffset: 350;
  }
  to {
    stroke-dashoffset: 0;
  }
}

